import React from 'react';
import Latex from 'react-latex';
export default function DecomposicaoCholesky(){ return(<>

<h2>
    Decomposicao de Cholesky
</h2>

Seja A uma matriz definida positiva, então ela pode ser decomposta em

<Latex displayMode={true}>{'$$ A = GG^t $$'}</Latex>

Sendo que G é uma matriz triangular inferior, chamada de fator de Cholesky.

<br /><br />

Podemos resolver um sistema linear a partir dessa decomposição, dessa forma

<Latex displayMode={true}>{'$$'+
'A\\vec{x}=\\vec{b} \\Leftrightarrow '+
'GG^T\\vec{x}=\\vec{b} \\Rightarrow' +
'\\begin{cases} G\\vec{y} = \\vec{b} \\\\ G^T\\vec{x}=\\vec{y}\\end{cases}'
+'$$'}</Latex>


A partir da matriz A, podemos formar a matriz G, dessa forma:


<Latex displayMode={true}>{'$$'+
'G='+
'\\begin{pmatrix} '+
'\\sqrt{a_{11}}&0&0&0&0&0\\\\[.4em] '+
'\\frac{a_{21}}{g_{11}}&\\sqrt{a_{22}-g_{21}^2}&0&0&0&0\\\\[.4em]'+
'\\vdots&\\frac{a_{32}-g_{21}g_{21}}{g_{22}}&\\sqrt{a_{33}-\\sum^{3-1}_{k=1}g_{3k}^2}&0&0&0\\\\[.4em]'+
'\\vdots&\\vdots&\\frac{a_{43}-\\sum^{3-1}_{k=1}g_{4k}g_{3k}}{g_{33}}&\\sqrt{a_{44}-\\sum^{4-1}_{k=1}g_{4k}^2}&0&0\\\\[.4em]'+
'\\vdots&\\vdots&\\vdots&\\frac{a_{54}-\\sum^{4-1}_{k=1}g_{5k}g_{4k}}{g_{44}}&\\sqrt{a_{jj}-\\sum^{j-1}_{k=1}g_{jk}^2}&0\\\\[.4em]'+
'\\vdots&\\vdots&\\vdots&\\vdots&\\frac{a_{ij}-\\sum^{j-1}_{k=1}g_{ik}g_{jk}}{g_{jj}}&\\ddots\\\\[.4em]'+
'\\frac{a_{n1}}{g_{11}}&\\frac{a_{n1}-g_{n1}g_{21}}{g_{22}}&\\frac{a_{n3}-\\sum^{3-1}_{k=1}g_{nk}g_{3k}}{g_{33}}&\\frac{a_{n4}-\\sum^{4-1}_{k=1}g_{nk}g_{4k}}{g_{44}}&\\vdots&\\vdots'+
'\\end{pmatrix}'
+'$$'}</Latex>

</>)}